.backToTop{
    border-radius: 50%;
    cursor: pointer;
    position: fixed;
    bottom: 100px;
    right: 30px;
    height: 54px;
    width: 54px;
    background: -webkit-linear-gradient(145deg,#f81f01,#ee076e);
    padding: 17px 10px;
    text-transform: uppercase;
    font-weight: 900;
    color: #fff;
    font-size: 9px;
    text-align: center;
    .chevron{
        border-style: solid;
        border-width: 5px 5px 0 0;
        content: "";
        display: inline-block;
        height: 17px;
        left: 0px;
        position: relative;
        top: 5px;
        transform: rotate(-45deg);
        vertical-align: top;
        width: 17px;
    }
}

