
.country-details{
    background: #f8f9fe;
    position: relative;
    &::before{
        background: rgba(255,255,255,0.92);
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }

    .country-header{
        position: relative;
        padding:15px 0;
        h2{
           color:  #1f1f25;
            font-size: 34px;
            font-weight: 700; 
        }
        img{
            height: 64px;
            width: 64px;
        }
    }
    .con-details{
        display: grid;
        grid-template-columns: auto auto auto auto;
        grid-gap: 10px;
        margin-bottom: 40px;
        div{
            text-align: center;
            padding: 20px 0;
            font-size: 30px;
            h2{
                color: #1f1f25;
                font-size: 30px;
                font-weight: 700;
                span{
                    display: block;
                    font-size: 22px;
                }
            }
            .total{
                span{
                    color:#1800ff
                }
            }
            .tactive{
                span{
                    color:#ff7700
                }
            }
            .recovered{
                span{
                    color:green
                }
            }
            .death{
                span{
                    color:red
                }
            }
        }
    }
    button{
        margin:0 0 20px 0;
          a{
            color: #212529;
            text-decoration: none;
          }
    }
    .ind-chart-container{
        div{
            margin-bottom: 30px;
        }
        div:nth-child(1){
            canvas{
                background: rgba(24,0,255,0.08);
            }
        }
        div:nth-child(2){
            canvas{
                background: rgba(255,2,2,0.08);
            }
        }
        div:nth-child(3){
            canvas{
                background: rgba(3, 128, 1, 0.08);
            }
        }
        div:nth-child(4){
            canvas{
                background: rgba(255,104,180,0.08);
            }
        }
    }
}

.corona-free{
    position: relative;
    min-height: calc(100vh - 180px);
    h2, h4{
        font-weight: 600; 
        margin-bottom: 20px;
    }

    h4{
        font-weight: 500; 
    }
}



@media screen and (max-width:767px){
    .country-details{
        .con-details{
            grid-template-columns: auto ;
            div{
                padding:5px 0;
            }
        }
    }
}
