.browse-country{
    background: url(../../images/world.png);
    background-position: center;
    position: relative;
    padding-top: 20px;
    &::before{
        position:absolute;
        content: '';
        background: rgba(255,255,255,0.84);
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
    }
}


.table-wrap{
    background: #f8f9fe;
    padding: 30px 0;
}

.table-wrap table{
    margin-bottom: 0;
}

.table-wrap table th{
    font-weight: 700;
}

.table-wrap table td{
    font-weight: 600;
}

.text-center{
    text-align: center;
}

.green{
    color: #02d05d;
    text-shadow: 2px 8px 6px rgba(0,0,0,0.2),
                 0px -5px 35px rgba(255,255,255,0.3);
}

.red {
    color: #f30404;
    text-shadow: 2px 8px 6px rgba(0,0,0,0.2),
                 0px -5px 35px rgba(255,255,255,0.3);
}

.yellow{
    color: #f1c00b;
    text-shadow: 2px 8px 6px rgba(0,0,0,0.2),
                 0px -5px 35px rgba(255,255,255,0.3);
}

.skyblue{
    color: #0ce0e0;
    text-shadow: 2px 8px 6px rgba(0,0,0,0.2),
                 0px -5px 35px rgba(255,255,255,0.3);
}

.country-image{
    display: inline-block;
    margin-right: 5px;
}

.serious{
    color: rgba(25,184,255);
    text-shadow: 2px 8px 6px rgba(0,0,0,0.2),
                 0px -5px 35px rgba(255,255,255,0.3);
}

.green-bg{
    background: green;
}

.get-btn.btn-outline-warning{
    font-size: 10px;
}

.get-btn{
    border : 1px solid #ffc107;
    border-radius: 2px;
    color: #ffc107;
    display: inline-block;
    font-size: 11px;
    font-weight: 600;
    padding: 6px 10px;
    text-decoration: none;
    
}

.get-btn:hover, .get-btn.active{
    background:#ffc107 ;
    color: #212529;
    text-decoration: none;
}

.container.datatableWrapper{
    padding: 20px 0;
    min-height: calc(100vh - 120px);
}
.dataTables_filter{
    text-align:right;
}
.pagination{
    float: right;
}

.pagination li a{
    cursor: pointer;
}

.page-item.active .page-link{
    background: linear-gradient(145deg,#31373d,#31373d) !important;
    border-color: #31373d !important;
}
tbody, thead{
    background-color: #343a40;
}


.table-bordered td, .table-bordered th {
    text-align: center;
    border: 1px solid #dee2e6;
    border-color: #454d55;
    color: #fff;
}
.table-bordered td, .table-bordered th{
    border-color: #454d55 !important;
}

.table-bordered td:nth-child(3), .table-bordered th:nth-child(3){
    color: #0ce0e0;
    font-weight: 600;
}

.table-bordered td:nth-child(4),  .table-bordered th:nth-child(4){
    color: #f1c00b;
    font-weight: 600;
}
.table-bordered td:nth-child(5),  .table-bordered th:nth-child(5){
    color: #02d05d;
    font-weight: 600;
}
.table-bordered td:nth-child(6),  .table-bordered th:nth-child(6){
    color:#f30404;
    font-weight: 600;
}

.table-bordered td:nth-child(7),  .table-bordered th:nth-child(7){
    color:#19b8ff;
    font-weight: 600;
}


.dataTables_wrapper .custom-select.custom-select-sm.form-control.form-control-sm{
    margin-left: 0 !important;
}   

@media screen and (max-width:767px){
    .dataTables_wrapper{
        .row{
            margin: 0;
            div.col-sm-12{
                width: 98%;
                margin: 0 auto;
                padding: 0;
                overflow-x: scroll;
            
            }
        }
        .dataTables_length.bs-select, .dataTables_info{
            display: none;
        }
        .pagination{
            float: none;
            align-items: center;
            justify-content: center;
        }
        .dataTables_filter {
            text-align: center;
        }
    }
}
