.not-found{
    background: #f8f9fe;
    height: calc(100vh - 120px);
    .wrapper{
        display: flex;
        justify-content: center;
        align-items: center;
      h1{
        font-size: 40px;
        font-weight: 800;
        padding-top: 60px;
        text-transform: uppercase;
      }
      button{
          margin-top: 30px;
          a{
            color: #212529;
            text-decoration: none;
          }
      }
    }
}

